___CSS_LOADER_EXPORT___.push([module.id, `*{
    padding: 0;
    border: none;
    box-sizing: border-box;
    margin: 0;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

body{
    height: 100vh;
    overflow: hidden;
}
nav {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    font-weight: 600;
    height: 40px;
    background-color: rgb(192, 192, 192);
}
.content {
    display: grid;
    grid-template-columns: 240px auto;

    height: 100%;
}
.sidebar {
    height: 100%;
    position: sticky;
}


.sidebar{
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
}
.sidebar button{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 16px;
    width: 200px;
    height: 40px;
}

.main{
    display: flex;
    flex-direction: column;
    background-color: rgb(230, 230, 230);
    padding: 40px;
    overflow: scroll;
}
.projects {
    width: 100%;
    background-color: rgb(251, 251, 251);
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;
    font-size: 24px;
    margin-bottom: 20px;
}
.card {
    padding-left: 20px;
    width: 100%;
    height: 40px;
    display: flex;
    align-items: center;
    background-color: gray;
    margin-bottom: 4px ;
    color: white;
    font-weight: 400;
}

dialog{
    position: absolute;
    top: 50%;
    left: 50%;
    height: 160px;
    width: 600px;
    transform: translate(-50%, -50%);

}

.content-createDialog{
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: center;
    justify-content: center;
}
dialog input {
    background-color: rgb(244, 244, 244);
    margin-left: 20px;
    height: 40px;
    width: 400px;
    border: none;
    outline: none;
    padding: 20px;
    font-size: 16px;

}
dialog button {
    width: 400px;
    height: 40px;
    font-size: 16px;
    font-weight: 500;
}
#ToDoDialog {
    height: 400px;
}
#ToDoDialog .input , #showEvent .input{
    width: 480px;
    display: grid;
    grid-template-columns: 80px 400px;
    align-items: center;
}
dialog textarea , dialog select{
    width: 400px;
    height: 80px;
    background-color: rgb(244, 244, 244);
    resize: none;
    border: none;
    outline: none;
    padding: 10px;
    font-size: 16px;
}
#ToDoDialog input , #showEvent input{
    margin-left: 0;
    padding: 10px;
}
.textareaInput{
    display: grid !important;
    align-items: start !important;
}
dialog select{
    height: 40px;
}

.todo-card{
    display: grid;
    grid-template-columns: 3fr 1fr 1fr 1fr;
    align-content: center;
    justify-items: center;
}

.todoname {
justify-self: start;
}

.buttons {
    display: flex;
    gap: 40px;
}


#showEvent{
    height: 440px;
}

#Completed {
    width: 440px;
}
.buttons button{
    width: 200px;
}

#Remove{
    color: red;
}

#Confirm{
    color: green;
}

.classification{
    background-color: rgb(107, 107, 107);
    color: white;
}
.classification-list{
    display: flex;
    flex-direction: column;
    gap: 2px;
    align-items: center;
}`, "",{"version":3,"sources":["webpack://./src/styles/style.css"],"names":[],"mappings":"AAAA;IACI,UAAU;IACV,YAAY;IACZ,sBAAsB;IACtB,SAAS;IACT,gDAAgD;AACpD;;AAEA;IACI,aAAa;IACb,gBAAgB;AACpB;AACA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,eAAe;IACf,gBAAgB;IAChB,YAAY;IACZ,oCAAoC;AACxC;AACA;IACI,aAAa;IACb,iCAAiC;;IAEjC,YAAY;AAChB;AACA;IACI,YAAY;IACZ,gBAAgB;AACpB;;;AAGA;IACI,gBAAgB;IAChB,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,SAAS;AACb;AACA;IACI,gDAAgD;IAChD,eAAe;IACf,YAAY;IACZ,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,oCAAoC;IACpC,aAAa;IACb,gBAAgB;AACpB;AACA;IACI,WAAW;IACX,oCAAoC;IACpC,YAAY;IACZ,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,gBAAgB;IAChB,eAAe;IACf,mBAAmB;AACvB;AACA;IACI,kBAAkB;IAClB,WAAW;IACX,YAAY;IACZ,aAAa;IACb,mBAAmB;IACnB,sBAAsB;IACtB,mBAAmB;IACnB,YAAY;IACZ,gBAAgB;AACpB;;AAEA;IACI,kBAAkB;IAClB,QAAQ;IACR,SAAS;IACT,aAAa;IACb,YAAY;IACZ,gCAAgC;;AAEpC;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,mBAAmB;IACnB,uBAAuB;AAC3B;AACA;IACI,oCAAoC;IACpC,iBAAiB;IACjB,YAAY;IACZ,YAAY;IACZ,YAAY;IACZ,aAAa;IACb,aAAa;IACb,eAAe;;AAEnB;AACA;IACI,YAAY;IACZ,YAAY;IACZ,eAAe;IACf,gBAAgB;AACpB;AACA;IACI,aAAa;AACjB;AACA;IACI,YAAY;IACZ,aAAa;IACb,iCAAiC;IACjC,mBAAmB;AACvB;AACA;IACI,YAAY;IACZ,YAAY;IACZ,oCAAoC;IACpC,YAAY;IACZ,YAAY;IACZ,aAAa;IACb,aAAa;IACb,eAAe;AACnB;AACA;IACI,cAAc;IACd,aAAa;AACjB;AACA;IACI,wBAAwB;IACxB,6BAA6B;AACjC;AACA;IACI,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,sCAAsC;IACtC,qBAAqB;IACrB,qBAAqB;AACzB;;AAEA;AACA,mBAAmB;AACnB;;AAEA;IACI,aAAa;IACb,SAAS;AACb;;;AAGA;IACI,aAAa;AACjB;;AAEA;IACI,YAAY;AAChB;AACA;IACI,YAAY;AAChB;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,oCAAoC;IACpC,YAAY;AAChB;AACA;IACI,aAAa;IACb,sBAAsB;IACtB,QAAQ;IACR,mBAAmB;AACvB","sourcesContent":["*{\n    padding: 0;\n    border: none;\n    box-sizing: border-box;\n    margin: 0;\n    font-family: Verdana, Geneva, Tahoma, sans-serif;\n}\n\nbody{\n    height: 100vh;\n    overflow: hidden;\n}\nnav {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    font-size: 24px;\n    font-